<template>
  <div class="playListTopInfo">
    <div class="bgBox" v-if="isShowBgBox">
      <div class="bgImg">
        <img :src="playListData.bgImg" alt="" />
      </div>
      <div class="img">
        <img :src="playListData.bgImg" alt="" />
        <span class="bb">{{ playListData.playCount | format }}</span>
      </div>
      <div class="desc">
        <div class="text">{{ playListData.title }}</div>
        <div class="author">
          <div class="authorImg">
            <img :src="playListData.authorImg" alt="" />
          </div>
          <span>{{ playListData.authorName }}</span>
        </div>
      </div>
    </div>
    <Loading v-if="isShowLoading"></Loading>
  </div>
</template>

<script>
import Loading from "../components/Loading";

export default {
  name: "PlayListTopInfo",
  data() {
    return {
      isShowLoading:true,
      isShowBgBox:false,
    };
  },
  props: {
    playListData: {
      type: Object,
      default(){
          return {
            bgImg: "",
            authorImg: "",
            playCount: 0,
            authorName: "",
            title: "",
            musicList:[]
        }
      }
    },
  },
  created(){
    this.isShowLoading = false;
    this.isShowBgBox = true;
  },
//   activated(){
//       console.log(this.playListData);
//     this.getPlayListData();
//   },
  
  components:{
      Loading
  },
  filters: {
    format(v) {
      return (v / 10000).toFixed(1) + "万";
    },
  },
};
</script>

<style lang="less" scoped>
.playListTopInfo {
  .bgBox{
      width: 100%;
      height: 200px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      position: relative;
    
      .bgImg {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        left: 0px;
        top: 0px;
        z-index: -1;
        img {
          filter: blur(20px);
          transform: scale(2);
        }
      }
      .img {
        width: 150px;
        height: 150px;
        position: relative;
        border: 1px solid #666;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
        span.bb {
          font-size: 12px;
          position: absolute;
          top: 2px;
          right: 3px;
          color: rgb(250, 248, 248);
          text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
          display: flex;
          &::before {
            content: "";
            display: inline-block;
            width: 20px;
            height: 20px;
            vertical-align: middle;
            margin-right: -6px;
            background: url(data:image/svg+xml;base64,)
              no-repeat 0px 0px/12px 12px;
          }
        }
      }
      .desc {
        width: 50%;
        height: 150px;
        .text {
          width: 100%;
          color: white;
          font-size: 20px;
        }
        .author {
          margin-top: 30px;
          display: flex;
          align-items: center;
          .authorImg {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            overflow: hidden;
            img {
              display: block;
              width: 40px;
              height: 40px;
            }
          }
          span {
            font-size: 16px;
            color: white;
            margin-left: 10px;
          }
        }
      }

  }
}
</style>